<template>
	<div>
		<div class="head">
			<ul class="head_ul">
				<li class="head_ul_l">本人承诺以下填报内容和提供的证件完全真实，如有不实，别人愿意承担由此产生的一切后果及相关信息。</li>
				<li class="head_ul_r"><buttom class="li_r_btn" @click="show">提交资料</buttom></li>
			</ul>
		</div>
		
		<div class="item" @click="goEach('基本信息')">
			<i></i>
			<span>基本信息</span>
			<span class="complete">进度100%</span>
		</div>
		<div class="item" @click="goEach('报考信息')">
			<i></i>
			<span>报考信息</span>
			<span class="undone">进度50%</span>
		</div>
		<div class="item" @click="goEach('现任单位')">
			<i></i>
			<span>现任单位</span>
			<span class="undone">进度50%</span>
		</div>
		<div class="item" @click="goEach('上一任单位')">
			<i></i>
			<span>上一任单位</span>
			<span class="undone">进度50%</span>
		</div>
		<div class="item" @click="goEach('上一任单位2')">
			<i></i>
			<span>上一任单位2</span>
			<span class="undone">非必填</span>
		</div>
		<div class="item"  @click="goEach('上一任单位3')">
			<i></i>
			<span>上一任单位3</span>
			<span class="undone">非必填</span>
		</div>
		<div class="item" @click="goEach('上一任单位4')">
			<i></i>
			<span>上一任单位4</span>
			<span class="undone">非必填</span>
		</div>
		<div class="item" @click="goEach('证件上传')">
			<i></i>
			<span>证件上传</span>
			<span class="undone">0%</span>
		</div>
		
		<div class="attention">
			<p class="attention_p">注意：</p>
			<p class="attention_p">现任单位为现在最近工作时间，按照时间依次往上一任单位推，直至符合报考工种年限</p>
		</div>
		
		<Bottom></Bottom>
	</div>
	
		
	
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				
			}
		}, 
		methods:{
			goEach(text){
				if(text=='基本信息'){
					this.$router.push({name: 'basicinformation'})
				}else if(text=='报考信息'){
					this.$router.push({name: 'information'})
				}else if(text=='现任单位'){
					this.$router.push({name: 'Thecurrentunit'})
				}else if(text=='上一任单位'){
					this.$router.push({name: 'Intheunit_a'})
				}else if(text=='上一任单位2'){
					this.$router.push({name: 'Intheunit_b'})
				}else if(text=='上一任单位3'){
					this.$router.push({name: 'Intheunit_c'})
				}else if(text=='上一任单位4'){
					this.$router.push({name: 'Intheunit_d'})
				}else if(text=='证件上传'){
					this.$router.push({name: 'Uploadphotoid'})
				}
			},
			show(){
				this.$popup({
					title: '提交资料',
          			content: '请确保你的各项信息填写进度为100%，请确保你的证件已全部上传，并清晰提交后，本申请则进入待审核状态，在没有结果出来之前，无法进行修改。是否确认提交？'
				}).then((res)=>{
					console.log(res)
					if(res=='sure'){
						return;
					}
				})
			}
		}

		
	}

</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.head{width: 100%;height: 1.6rem;background-color: #fbad59;padding:0.3rem;}
	.head_ul{width: 100%;height: 0.8rem;height: 1.56rem;display: flex;}
	.head_ul_l{width: 70%;height: 0.8rem; font-size: 0.15rem;color: #ffffff;}
	.head_ul_r{width: 30%;height: 0.8rem;font-size: 0.12rem;color: #fa8a12;display: flex;justify-content: center;align-items: center;margin: 0 auto;}
	.li_r_btn{background-color: #fff;width: 1.7rem;height: 0.6rem;text-align: center;line-height: 0.6rem;border-radius: 0.3rem;}
	
	.item{width: 100%;height: 0.9rem;border-bottom: 0.01rem solid #e8e8e8;position: relative;line-height: 0.9rem;font-size: 0.3rem;padding: 0 0.3rem;box-sizing: border-box;
		i{width: 0.13rem;height: 0.23rem;background: url(../../assets/go.png) no-repeat;background-size:100% 100% ;position: absolute;right: 0.3rem;top: 38%;}
		.complete{color: #333333;float: right;margin-right: 0.3rem;}
		.undone{color: #fa8a12;float: right;margin-right: 0.3rem;}
	}
	
	.attention{width: 100%;height: 2.28rem;padding: 0.3rem;
	  .attention_p{font-size: 0.16rem;color: #fa8a12;}
	}
</style>


